<script>
import {postApi1} from "../../api/commonApi";

	export default {
		name: "add_task",
		components: {},
		data() {
			return {
				info: {
                    taskDetailName:'',
                    taskDescribe:'',
                    taskCheckList:[],
                    remindSetEntity:{
                        afterTime:'',
                        afterTimeUnit:'',
                        basePoint:'',
                        remindItemSet: {
                            repeatFlag:false,//是否重复
                            repeatType:"0",//重复了类型
                            repeatCount:"",//重复次数
                            repeatDeadline:"",//不再重复截止时间
                            intervalDays:""//间隔天数
                        }
                    }
                },
                checkInfo:'',
                isAdd_check:false,
                ischeck:false,
                type:'l',
                
                    isSet:false,
			};
		},
		mounted() {
        },
		methods: {
            //添加检查项
            addCheck(){
                this.info.taskCheckList.push(this.checkInfo)
                this.isAdd_check=false
                this.checkInfo=''
            },
            //保存子任务
            saveTask(info){
                this.$refs[info].validate((valid) => {
                if (valid) {
                    this.$emit('saveTask',this.info)
                    this.info={
                    taskDetailName:'',
                    taskDescribe:'',
                    taskCheckList:[],
                    remindSetEntity:{
                        afterTime:'',
                        afterTimeUnit:'',
                        basePoint:'',
                        remindItemSet: {
                            repeatFlag:false,//是否重复
                            repeatType:"0",//重复了类型
                            repeatCount:"",//重复次数
                            repeatDeadline:"",//不再重复截止时间
                            intervalDays:""//间隔天数
                        }
                    }
                }
                } else {
                    console.log('error submit!!');
                    return false;
                }
            })
            }
        }
	};
</script>

