				<p class="marGb">
					<lb-select v-model="info.fieldType" :popper-append-to-body="false" style="width:100%">
						<lb-option v-for="item in addlist" :label="item.dicValue" :value="item.dicKey" :key="item.dicKey" v-if="item.dicKey!=7&&item.dicKey!=6"></lb-option>
