<script>
import { postApi1, postApi, getApi1 } from "../../api/commonApi";

export default {
  name: "add_task",
  components: {},
  data() {
    return {
      isAdd: false,
      colList: [],
      info: {
        teamId: "34",
        fieldName: "",
        fieldType: "1",
        caseType: "",
        caseClass: "",
        type: 2,
        remark: "",
        mustFlag: false,
        minLimit: null,
        maxLimit: null,
        optionList: []
      },
      addlist: [],
      allList:[]
    };
  },
  mounted() {
    this.getAddList();
  },
  methods: {
    getAddList() {
      let that = this;
      getApi1(
        "system/sysDictionary/list",
        {
          companyId: "34",
          type: "0080"
        },
        function(data) {
          that.addlist = data.list;
        },
        function(data) {}
      );
    },
    //保存一条自定义字段
    save() {
      this.colList.push(this.info);
      this.info = {
        teamId: "34",
        fieldName: "",
        fieldType: "1",
        caseType: "",
        caseClass: "",
        type: 2,
        remark: "",
        mustFlag: false,
        minLimit: null,
        maxLimit: null,
        optionList: []
      };
      this.isAdd = false;
    },
    //添加选项
    addOption() {
      this.info.optionList.push({
        optionValue: "",
        optionKey: ""
      });
    },
    //删除字段
    deleteZiduan(id, item) {
      let that = this;
      this.$confirm("此操作将删除该字段, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          if (id) {
            for(let i of that.allList){
              if(i.customerFieldSetId==id){
                 that.allList.splice(that.allList.indexOf(i), 1);
                 that.colList.splice(that.colList.indexOf(item), 1);
              }
            }
          } else {
            that.colList.splice(that.colList.indexOf(item), 1);
          }
          that.$message({
            type: "info",
            message: "已删除"
          });
        })
        .catch(() => {
          that.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    //编辑自定义字段
    editZi(row){
        this.isAdd=true
        this.info=row
    },
    addTrue(){
        this.isAdd=true
        this.info={
        teamId: "34",
        fieldName: "",
        fieldType: "1",
        caseType: "",
        caseClass: "",
        type: 2,
        remark: "",
        mustFlag: false,
        minLimit: null,
        maxLimit: null,
        optionList: []
      }
    },
    getType(val){
        for(let i of this.addlist){
            if(i.dicKey==val){
              return i.dicValue
            }
        }
    }
  }
};
</script>

