<!--流程图左侧子任务列表 by wuyang 2020-8-31!-->
<template>
	<div class="childtask_list">
		<lb-container height="100%">
			<lb-collapse v-model="activeName">
				<lb-collapse-item class="caseitem" v-for="item,indexs in Stagelist" :name="item.id" :key="item.id">
					<template slot="title">
						<lb-tooltip :content="item.name" placement="top">
							<template>
								<span class="CaseStageList-title">{{ item.name }}
                  <!-- <a style="float:right" @click.stop="addZi(item)" title="自定义字段">自定义字段</a>
									<a style="float:right" title="设置提醒"><img src="sets.svg">设置提醒</a> -->
								</span>
							</template>
						</lb-tooltip>
            <lb-tooltip content="自定义字段" placement="top" style="float:right;margin-right:10px;margin-top:5px">
							<template>
									<a @click.stop="addZi(item)" class="hovera">
                  <img src="zidingyi_low.svg" class="hover_show">
                  <img src="zidingyi.svg" class="hover_hidden">
                  </a>
							</template>
						</lb-tooltip>
            <el-popover placement="bottom" :ref="indexs" trigger="manual"  width="400" v-model="openList[indexs]"  popper-class="noPadPop">
											<p class="el-dialog__header">设置提醒</p>
                      <p class="titles"><span>任务</span><span>时间</span></p>
											<div class="padp">
												<div class="remind_info">
													<span class="comline">当</span>
													<lb-select v-model="item.remindSetEntity.basePoint" style="width:160px">
														<lb-option v-for="item2 in stepList" :key="item2.key" :label="item2.value" :value="item2.key"></lb-option>
													</lb-select>
													<lb-input v-model="item.remindSetEntity.afterTime" style="width:60px"></lb-input>
													<lb-select v-model="item.remindSetEntity.afterTimeUnit" style="width:65px">
														<lb-option v-for="item2 in timeList" :key="item2.dicKey" :label="item2.dicValue" :value="item2.dicKey"></lb-option>
													</lb-select>
													<span class="comline">提醒</span>
												</div>
												<p class="remind_info">
													<lb-checkbox v-model="item.remindSetEntity.remindItemSet.repeatFlag" @change="item.remindSetEntity.remindItemSet.repeatFlag?item.remindSetEntity.remindItemSet.repeatType='1':item.remindSetEntity.remindItemSet.repeatType='0'" style="line-height:40px;padding:0 10px">重复</lb-checkbox>
													<lb-select v-model="item.remindSetEntity.remindItemSet.repeatType" style="width:112px" v-if="item.remindSetEntity.remindItemSet.repeatFlag" >
														<lb-option v-for="item2 in repeatTypeList" :key="item2.dicKey" :label="item2.dicValue" :value="item2.dicKey"></lb-option>
													</lb-select>
                          <lb-input v-if="item.remindSetEntity.remindItemSet.repeatType==4" style="width:60px" v-model="item.remindSetEntity.remindItemSet.intervalDays"></lb-input>
                          <span v-if="item.remindSetEntity.remindItemSet.repeatType==4" class="comline">天</span>
												</p>
												<p class="dialogcenter">
													<lb-button plain @click="$set(openList,indexs,false)" style="margin-right:20px">取消</lb-button>
													<lb-button @click="$set(openList,index,false)">确定</lb-button>
												</p>
											</div>
                         <a class="hovera" style="float:right;margin-top:5px;margin-right:10px;" slot="reference" @click.stop="$set(openList,indexs,true)">
                            <img src="sets_low.svg" class="hover_show">
                            <img src="sets.svg" class="hover_hidden">
                            </a>
										</el-popover>
            	
					</template>
					<draggable tag="div" v-model="item.list" v-bind="dragOptions" @start="isDragging = true" @end="
            isDragging = false;
            draggabletask(item.list);
          "
					 v-if="item.list.length>0" class="backs">
						<transition-group type="transition" name="flip-list">
							<div :key="index" @mouseenter="moveindex = index" @mouseleave="moveindex = '-1'" v-for="(items, index) in item.list">
								<div class="CaseStageList-task-left">
									<!-- <img
                  v-show="moveindex == items.taskDetailId"
                  src="../../assets/schedule/drag.svg"
                  alt=""
                /> -->
								</div>
								<div>
									<div class="CaseStageList-task-right-r zhuseHover">
										<lb-tooltip :content="items.taskDetailName" placement="top">
											<template>
												<span>{{ items.taskDetailName }}</span>
											</template>
										</lb-tooltip>
										<!-- <img src="icondele.svg" class="icondele" @click=> -->
                    	<a  class="hovera" @click="deleteTask(items,item.list)">
                        <img src="icondele.svg"  class="hover_show" >
                        <img src="icondele_hover.svg"  class="hover_hidden" >
                      </a>
                      <a  class="hovera" @click="editTask(items)">
                        <img src="bianji1.svg"  class="hover_show" >
                        <img src="bianji2.svg"  class="hover_hidden" >
                      </a>
										<el-popover placement="bottom" :ref="index"  trigger="click"  width="400"  popper-class="noPadPop">
											<p class="el-dialog__header">设置提醒</p>
											<div class="padp">
												<div class="remind_info">
													<span class="comline">当</span>
													<lb-select v-model="items.remindSetEntity.basePoint" style="width:160px">
														<lb-option v-for="item2 in stepList" :key="item2.key" :label="item2.value" :value="item2.key"></lb-option>
													</lb-select>
													<lb-input v-model="items.remindSetEntity.afterTime" style="width:60px"></lb-input>
													<lb-select v-model="items.remindSetEntity.afterTimeUnit" style="width:65px">
														<lb-option v-for="item2 in timeList" :key="item2.dicKey" :label="item2.dicValue" :value="item2.dicKey"></lb-option>
													</lb-select>
													<span class="comline">提醒</span>
												</div>
												<p class="remind_info">
													<lb-checkbox v-model="items.remindSetEntity.remindItemSet.repeatFlag" @change="items.remindSetEntity.remindItemSet.repeatFlag?items.remindSetEntity.remindItemSet.repeatType='1':items.remindSetEntity.remindItemSet.repeatType='0'" style="line-height:40px;padding:0 10px">重复</lb-checkbox>
													<lb-select v-model="items.remindSetEntity.remindItemSet.repeatType" style="width:112px" v-if="items.remindSetEntity.remindItemSet.repeatFlag" >
														<lb-option v-for="item2 in repeatTypeList" :key="item2.dicKey" :label="item2.dicValue" :value="item2.dicKey"></lb-option>
													</lb-select>
                          <lb-input v-if="items.remindSetEntity.remindItemSet.repeatType==4" style="width:60px" v-model="items.remindSetEntity.remindItemSet.intervalDays"></lb-input>
                          <span v-if="items.remindSetEntity.remindItemSet.repeatType==4" class="comline">天</span>
												</p>
												<p class="dialogcenter">
													<lb-button plain  style="margin-right:20px">取消</lb-button>
													<lb-button >确定</lb-button>
												</p>
											</div>
											<a slot="reference" class="hovera">
                        <img src="sets_low.svg" class="hover_show">
                          <img src="sets.svg" class="hover_hidden">
                      </a>
										</el-popover>
                    <!-- <p v-popover:popover>
												<span class="zhuse" v-if="items.remindSetEntity.basePoint&&items.remindSetEntity.afterTime&&items.remindSetEntity.afterTimeUnit">
													当{{getBase(items.remindSetEntity.basePoint)}}{{items.remindSetEntity.afterTime}}
                          {{getDay(items.remindSetEntity.afterTimeUnit)}}提醒</span>
												<span class="zhuse" style="margin-right:10px"><img src="sets.svg" class="commonicon"></span>
                        </p> -->
                    
									</div>
								</div>
								<div class="beise">
									<p v-for="item2 in items.taskCheckList" class="list">{{item2}}</p>
									<p class="zhuseHover check_hover checkLitts">
										<span @click="$set(items,'isaddCheck',true)" v-if="!items.isaddCheck">
											<img src="a-check.svg" class="a-check">
											<img src="a-check-s.svg" class="a-checks">添加检查项</span>
										<span v-else>
											<lb-input v-model="items.onecheck" :maxlength="20" show-word-limit @enter="addCheck(items)" /></span>
									</p>
								</div>
							</div>
						</transition-group>
					</draggable>
					<div class="CaseStageList-next">
						<div>
							<p>
								<span class="add_tasks" @click="addTask(indexs)"><i class="el-icon-circle-plus" />添加子任务</span>
							</p>
						</div>
					</div>
				</lb-collapse-item>
			</lb-collapse>
		</lb-container>
		<!--  -->
		<lb-dialog title="添加子任务" :visible.sync="isAdd" width="500px" class="heightFullDialog">
			<add-task ref="add_task" @saveTask="saveTask"></add-task>
		</lb-dialog>

      <lb-dialog title="自定义字段"  :visible.sync="isZid" width="500px" class="heightFullDialog">
          <field ref="fields" :list="colList"></field>
		</lb-dialog>
	</div>
</template>

<script>
	import {
		postApi,
		postApi1
	} from "../../api/commonApi";
	import addTask from "../add_task/add_task";
  import draggable from "vuedraggable";
	import field from "../field/field";
	export default {
		name: "childtask_list",
		components: {
      addTask,
      field
		},
		data() {
			return {
				activeName: [],
				Stagelist: [],
				isAdd: false,
				itemindex: "",
				onecheck: "",
				ischeck: false,
				isEdit: false,
				stepList: [],
				timeList: [{
						dicKey: "1",
						dicValue: "天"
					},
					{
						dicKey: "2",
						dicValue: "周"
					},
					{
						dicKey: "3",
						dicValue: "月"
					},
					{
						dicKey: "4",
						dicValue: "季度"
					},
					{
						dicKey: "5",
						dicValue: "年"
					}
				],
				repeatTypeList: [{
						dicKey: "1",
						dicValue: "每天"
					},
					{
						dicKey: "2",
						dicValue: "每周"
					},
					{
						dicKey: "3",
						dicValue: "每月"
					},
					{
						dicKey: "4",
						dicValue: "自定义"
					}
				],
        isSet: false,
         isZid:false,
         openList:[],
         openLists:[[]],
         colList:[]
			};
		},
		mounted() {
			this.getList();
      this.getStep(1);
      this.getCoList()
		},
		computed: {
			dragOptions() {
				return {
					animation: 1,
					disabled: false,
					ghostClass: "ghost"
				};
			}
    },
		methods: {
			//获取设置期限阶段
			getStep(val) {
				postApi1(
					"customer/field/set/fieldField", {
						caseClass: this.type == "l" ? 1 : 2,
						caseType: val
					},
					data => {
						this.stepList = data.list;
						// let list=data.list
						// this.info.remindSetEntity.basePoint=list[0].key
					},
					data => {}
				);
			},
			getList() {
        let that = this;
        console.log(this.$route)
				postApi(
					"/activitiModeler/getChildTaskByPhase?modelId="+this.$route.query.modelId, {},
					function(data) {
            that.Stagelist = data.data;
						for (let i of that.Stagelist) {
              i.remindSetEntity=i.remindSetEntity?i.remindSetEntity:{
                        afterTime:'',
                        afterTimeUnit:'1',
                        basePoint:'',
                        remindItemSet: {
                            repeatFlag:false,//是否重复
                            repeatType:"0",//重复了类型
                            repeatCount:"",//重复次数
                            repeatDeadline:"",//不再重复截止时间
                            intervalDays:""//间隔天数
                        }
                    }
              i.list = i.list ? i.list : [];
              for(let j of i.list){
                j.remindSetEntity=j.remindSetEntity?j.remindSetEntity:{
                        afterTime:'',
                        afterTimeUnit:'1',
                        basePoint:'',
                        remindItemSet: {
                            repeatFlag:false,//是否重复
                            repeatType:"0",//重复了类型
                            repeatCount:"",//重复次数
                            repeatDeadline:"",//不再重复截止时间
                            intervalDays:""//间隔天数
                        }
                    }
              }
						}
					},
					function(data) {
              this.$message({
							type: "error",
							message: data.msg
						});
					}
				);
			},
			//添加子任务
			addTask(val) {
				this.isAdd = true;
				this.isEdit = false;
				this.itemindex = val;
			},
			//保存返回子任务信息
			saveTask(val) {
				if (this.isEdit) {} else {
					this.Stagelist[this.itemindex].list.push(val);
				}
				this.isAdd = false;
				console.log(this.Stagelist);
			},
			//添加检查项
			addCheck(item) {
				item.taskCheckList.push(item.onecheck);
				item.isaddCheck = false;
				item.onecheck = "";
			},
			//删除子任务
			deleteTask(items, item) {
				this.$confirm("是否确定删除该子任务?", "提示", {
						confirmButtonText: "确定",
						cancelButtonText: "取消",
						type: "warning"
					})
					.then(() => {
						item.splice(item.indexOf(items), 1);
						this.$message({
							type: "success",
							message: "删除成功!"
						});
					})
					.catch(() => {
						this.$message({
							type: "info",
							message: "已取消删除"
						});
					});
			},
			//编辑子任务
			editTask(item) {
				this.isAdd = true;
        this.isEdit = true;
        this.$nextTick(function(){
			  	this.$refs.add_task.info = item;
        })
      },
			getBase(val) {
				for (let i of this.stepList) {
					if (i.key == val) {
						return i.value
					}
				}
      },
       getDay(val){
          for(let i of this.timeList){
            if(val==i.dicKey){
              return i.dicValue
            }
          }
        },
        //打开自定义字段弹框，获取列表
        addZi(item){
          let that=this
          if(item.id){
            that.isZid=true
          }else{
            this.isZid=true
          }
        },
        //获取自定义字段列表
        getCoList(){
             let that=this
              postApi("/activitiModeler/getCustomerField?modelId=5732501&companyId=34",{},function(data){
                that.colList=data.data               
              },function(data){
                
              })
        }
		}
	};
</script>

<style scope lang="scss">
	@import "childtask_list.scss";
</style>
<style lang="less">
	.childtask_list {
		.el-collapse-item__header {
			font-size: 14px;
			color: #999;
			font-weight: normal;
			border-bottom: 1px solid #e5e5e5;
		}

		.el-collapse-item__wrap {
			border-bottom: 1px solid #e5e5e5;
			padding: 14px 0px 10px 20px;
		}

		.is-active {
			color: #2e6e75;
		}
  }
  .hovera{
    .hover_show{
      display: inline-block;
    }
    .hover_hidden{
      display: none;
    }
    &:hover{
      .hover_show{
      display:none;
    }
    .hover_hidden{
      display: inline-block;
    }
    }
  }
</style>
