<template>
  <div id="app">
    <p class="saveBut">
      <lb-button @click="showModal">保存</lb-button>
    </p>
    <div style="display:flex;height:calc(100% - 52px)">
      <childtask-list ref="childtask"></childtask-list>
      <Demo ref="flow" @change="outputData" class="flow"/>
      <lb-dialog
        title=""
        :visible.sync="visible"
        :close-on-click-modal="false"
        :show-close="false"
        width="500px">
        <lb-form
          @keyup.native.enter="conformSubmit"
          @submit.native.prevent
          ref="newNodeSetting">
          <lb-form-item
            label="名称"
            prop="desc">
            <lb-input
              v-model="name"
              placeholder="请输入名称">
            </lb-input>
          </lb-form-item>
          <lb-form-item
            label="描述"
            type="textarea"
            prop="desc">
            <lb-input
              v-model="desc"
              placeholder="请输入描述">
            </lb-input>
          </lb-form-item>
        </lb-form>
        <span
        slot="footer"
        class="dialog-footer">
          <lb-button
            @click="cancelDrawerConf">
            取 消
          </lb-button>
          <lb-button
            type="primary"
            style="margin-left:10px;"
            @click="conformSubmit">
            确 定
          </lb-button>
        </span>
      </lb-dialog>
    </div>
  </div>
</template>

<script>
import flowDashboard from './components/Flow'
import childtaskList from './components/childtask_list/childtask_list'
import { sentData } from './api/article'
import Demo from './components/Demo'
export default {
  name: 'app',
  components: { 
    flowDashboard,
    childtaskList,
    Demo
  },
  data() {
    return {
      visible:false,
      name:'',
      desc:''
    }
  },
  mounted() {
    // this.loadData();
  },
  methods: {
    outputData(data){
     console.log("data______",data)
    },
    async loadData(postData){
      let res = await sentData({
        ...postData
      });
      console.log(res);
      resetFormData();
      if(res.msg === 'success'){
        window.location.href = '';
      }
    },
    resetFormData(){
     this.desc = '';
     this.name = '';
    },
    showModal(){
      this.visible = true;
      this.resetFormData();
    },
    cancelDrawerConf(){
      this.visible = false;
      this.resetFormData();
    },
    conformSubmit(){
      this.visible = false;

      postData.description = this.desc;
      postData.modelName = this.name;
      postData.agentPoint = this.$route.query.agentPoint;
      postData.caseClass = this.$route.query.caseClass;
      postData.casePhaseNo = this.$route.query.casePhaseNo;
      postData.caseType = this.$route.query.caseType;
      postData.companyId = this.$route.query.companyId;
      postData.createUser = this.$route.query.createUser;
      postData.modelId = this.$route.query.modelId;
      postData.processFlag = this.$route.query.processFlag;
      postData.processType = this.$route.query.processType;

      let postData = this.$refs.flow.postData;
      let allChildTask = this.$refs.childtask.Stagelist;
      postData.allChildTask = JSON.stringify(allChildTask);
      this.loadData(postData);

    }
  }
}
</script>

<style>
@import 'style/root.css';
#minder-container {
  height: 100vh;
}

</style>
