    text-align: right;
    padding: 10px 20px;
  } 
  .childtask_list{
    width: 350px;
  }
  .flow{
    flex:1;/*这里设置为占比1，填充满剩余空间*/  
    border: 1px solid #d9d9d9;
  }
}
