import axios from 'axios'
import { Message } from 'element-ui'

//const apiPath = process.env.VUE_APP_API_FLOW;
const apiPath = process.env.VUE_APP_API_TEST;
const service = axios.create({
  baseURL: apiPath, // url = base url + request url
  // withCredentials: true, // send cookies when cross-domain requests
  timeout: 50000 // request timeout
})

// request interceptor
service.interceptors.request.use(
  config => {
    if (window.require) { //如果是桌面程序 使用localStorage
      config.headers['token'] = localStorage.getItem('token')
  
    } else { //web端
      config.headers['platform']='PC' //版本
      config.headers['version']='1.9.0'//版本号
      config.headers['token']='d00b71526037da46ab12100de89343e4'
      // config.headers['token'] = getToken();
      // console.log(config.headers.token);
      // sessionStorage.setItem('token', JSON.stringify(config.headers.token)); //将token存储于sessionStorage中  放于上传插件请求header中
    }

    return config
  },
  error => {
    console.log(error) // for debug
    return Promise.reject(error)
  }
)

// response interceptor
service.interceptors.response.use(
  response => {
    const res = response.data
    return res
  },
  error => {
    console.log('err' + error) // for debug
    Message({
      message: error.message,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(error)
  }
)

export default service
