import request from '@/utils/request1'
import request1 from '@/utils/request2'


export function getApi(url, params, successFunction, failFunction) {
  return request({
    url: url,
    method: 'get',
    params: params
  }).then((request) => {
    processReturn(request, url, params, successFunction, failFunction)
  })
}

export function postApi(url, params, successFunction, failFunction) {
  return request({
    url: url,
    method: 'post',
    data: params
  }).then((request) => {
    processReturn(request, url, params, successFunction, failFunction)
  })
}

