export function commonApi(url,method,responseType,params,successFunction,failFunction) {
	if(responseType == null || responseType == undefined){
		responseType = 'application/json'
	}
	return request({
    url: url,
    method: method,
		responseType: responseType,
    data: params
  }).then(function(request){
  	if(request.data.code == 0){
  			successFunction(request.data);
    	}else{
    		failFunction(request.data);
    	}
   })
}
