export function downloadApi(url,responseType,params,successFunction,processFun) {
  if(responseType == null || responseType == undefined){
   responseType = 'application/json'
   }
   var timestamp=new Date().getTime()
  return request({
     url: url,
   responseType: responseType,
     params: params,
     method:'get',
     onDownloadProgress(a) {
       console.log(a)
       var percent = parseInt(100 * (a.loaded / a.total));
       let datt={
        percent:percent,
        timestamp:timestamp
       }
       processFun(datt);
     }
   }).then(function(request){
   successFunction(request.data);
    })
 }


